import React, { useState, useEffect, Fragment } from 'react';
import bridge from '@vkontakte/vk-bridge';
import '@vkontakte/vkui/dist/vkui.css';

import {
	AppRoot,
	SplitLayout, SplitCol, View, Panel, PanelHeader,
	Root, Group, Avatar, Snackbar, Div, IconButton, 
	FixedLayout, Separator, Tabs, TabsItem, Counter, 
	Text, Tabbar, TabbarItem, Banner, Footer, Header,
	PopoutWrapper, Placeholder, MiniInfoCell, ScreenSpinner,
	Spinner, Button, Link
} from "@vkontakte/vkui";

import {
	Icon20CancelCircleFillRed, Icon24BrowserBack, Icon28CubeBoxOutline,
	Icon28More, Icon28HomeOutline, Icon24Dismiss, Icon20CloudArrowUpOutline,
	Icon56FileBrokenOutline, Icon24PictureInPictureOut16x9Outline,
	Icon56ArrowRightDoorOutline
} from "@vkontakte/icons";

const access = [
	363163789 // Я
];

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			// APP_INFO
			activeView: 'errors',
			activePanel: 'whitePage',
			activeTab: 'updates',
			snackbar: null,
			popout: null,

			// USER_INFO
			user: null,
			id: null,

			error: null,

			buttonShowError: false
		}
		this.replButtonError = this.replButtonError.bind(this);
	}

	goPanel(panel) {
		this.setState({ activePanel: panel });
	}

	goPage(view, panel) {
		this.setState({ activeView: view, activePanel: panel });
	}

	sendPush(text, icon) {
		if (icon == "cancel") icon = <Icon20CancelCircleFillRed width={26} height={26} />;

		this.setState({
			snackbar: (
				<Snackbar
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={
						<Avatar size={26} >
							{icon}
						</Avatar>
					}
				>
					{text}
				</Snackbar>
			),
		});
	}

	replButtonError() {
		if(this.state.buttonShowError == true) {
			this.setState({buttonShowError: false});
		} 
		if(this.state.buttonShowError == false) {
			this.setState({buttonShowError: true});
		}
	}

	setPopout(type, text) {
		if(type == "getVersionInfo") {
			this.setState({snackbar: null, popout: <PopoutWrapper onClick={() => this.setState({popout: null})}>
				<div
				  style={{
					backgroundColor: "var(--background_content)",
					borderRadius: 8,
					width: "85%",
					position: "relative",
					padding: "12px",
				  }}>
					<Placeholder style={{"font-size": "18px"}} icon={<Icon28CubeBoxOutline width={56} height={56}/>}>
						Информация о текущей версии приложения            
					</Placeholder>

				  	<Group>
					  	<MiniInfoCell before={<Icon24PictureInPictureOut16x9Outline />} after={<Counter size="s">Beta</Counter>} textWrap="full">
							<Text style={{"font-size": "17px"}}> Версия приложения: 1.0.0 </Text> 
						</MiniInfoCell> 

						<MiniInfoCell before={<Icon20CloudArrowUpOutline width={24} height={24} />} textWrap="full">
							<Text style={{"font-size": "17px"}}> Дата обновления: 24.02.2022 </Text> 
						</MiniInfoCell>
				  	</Group>
				</div>
			  </PopoutWrapper>});
		}

		if(type == "loadingPage") {
			this.setState({snackbar: null, popout: <PopoutWrapper>
				<div
				  style={{
					backgroundColor: "var(--background_content)",
					borderRadius: 8,
					width: "auto",
					position: "relative",
					padding: "12px",
				  }}>
					  <Div>
					  		<Spinner size="large" style={{ margin: "20px 0" }} /> 
						  	<Text style={{ "text-align": "center", "color": "gray" }}> {text} </Text>
					  </Div>
					  
				</div>
			  </PopoutWrapper>
			});
		}
	}

	quotApp() {
		bridge.send("VKWebAppClose");
	}

	componentDidMount() {
		this.setPopout("loadingPage", "Получаем данные о Вашем профиле...");

		bridge.send('VKWebAppGetUserInfo')
			.then(data => {
				setTimeout(() => {
					this.setState({ user: data, id: data.id, popout: null });
					if(~access.indexOf(data.id)) {
						this.goPage("home", "main");
					} else {
						this.setState({error: "Error 1: The user is not found in the system. Access denied."})
						this.goPage("errors", "main");
					}
				}, 2000);
			})
			.catch(err => { 
				setTimeout(() => {
					this.setState({activeView: "errors", activePanel: "main", error: err});
				}, 2000);
			});
		
		// Error 1: The user is not found in the system. Access denied. IS ID
		// Error 2: Further work is not possible without your data. Access denied. NOT IS ID
		// Error 
	}

	render() {
		return (
			<AppRoot>
				<SplitLayout popout={this.state.popout}>
					<SplitCol>
							<Root activeView={this.state.activeView} style={{"margin-bottom": "3%"}}>
							
								<View id="home" activePanel={this.state.activePanel}>
									<Panel id="main">
										<PanelHeader>Главная</PanelHeader>
										<Group>
											<Placeholder icon={<Icon56FileBrokenOutline width={58} height={58}/>}>
												<Text style={{ "font-size": "19px" }}>Ведутся тех. работы</Text>          
											</Placeholder>
										</Group>
									</Panel>
								</View>

								<View id="more" activePanel={this.state.activePanel}>
									<Panel id="main">
										<PanelHeader>Ещё</PanelHeader>
										<Group header={<Header style={{ color: "gray" }}>Версии</Header>}>
												<Banner
													header="Версии ChatManager Bot"
													subheader="Новые и прошлые версии чат-менеджера"
													asideMode="expand"
													onClick={() => this.sendPush("Данной страницы не существует или она была перемещена.", "cancel")}
													before={<Avatar style={{"background-color": "#d4b8d9af"}}
													size={38}>
														<Icon28CubeBoxOutline fill="black" width={34} height={34} />
													</Avatar>}
												/>
												<Banner
													header="Версия приложения"
													subheader="Информация о текущей версии приложения"
													asideMode="expand"
													onClick={() => this.setPopout("getVersionInfo")}
													before={<Avatar style={{"background-color": "#d4b8d9af"}}
													size={38}>
														<Icon28CubeBoxOutline fill="black" width={34} height={34} />
													</Avatar>}
												/>
										</Group>
										<Footer>Версия приложения: 1.0.0</Footer>
									</Panel>
									<Panel id="updateV">
										<PanelHeader left={<IconButton onClick={() => this.goPanel("main")} >
											<Icon24BrowserBack fill="612db5" />
										</IconButton>}>UpVs</PanelHeader>

										<Group>
											<Div>
												{(() => {
													if (this.state.activeTab == "updates") {
														return <Div style={{ color: "gray" }}>Обновлений нет</Div>;
													} else {
														return <Div style={{ color: "gray" }}>Исправления есть!</Div>;
													}
												})()}
											</Div>

											<FixedLayout filled vertical="bottom">
												<Separator wide />
												<Tabs>
													<TabsItem
														selected={this.state.activeTab === "updates"}
														onClick={() => this.setState({ activeTab: "updates" })}
														after={<Counter mode="prominent" size="s">+</Counter>}
													>
														Обновления
													</TabsItem>
													<TabsItem
														selected={this.state.activeTab === "corrections"}
														onClick={() => this.setState({ activeTab: "corrections" })}
														after={<Counter mode="primary" size="s">-</Counter>}
													>
														Исправления
													</TabsItem>
												</Tabs>
											</FixedLayout>
										</Group>
									</Panel>
								</View>

								<View id="errors" activePanel={this.state.activePanel}>

									<Panel id="main">
										<PanelHeader>Chat Manager</PanelHeader>
										<div style={{
											backgroundColor: "var(--background_content)",
											borderRadius: 8,
											width: "auto",
    										"vertical-align": "center",
											position: "relative",
											display: "flex",
											flexDirection: "column",
											justifyContent: "center",
											alignItems: "center",
											textAlign: "center",
											padding: "12px",
										}}>
											<Div>
												<Icon24Dismiss width={68} height={68} fill="#ff0f00d1" />
											</Div> 

											<Div>
												<Text style={{ "text-align": "center", "color": "gray", "font-size": "19px" }}> Произошла ошибка при выдаче прав доступа. </Text>
											</Div>

											<Div>
												<Button size="m" mode="secondary" onClick={this.replButtonError}>
													{this.state.buttonShowError ? "Скрыть ошибку" : "Показать ошибку"}
												</Button>
											</Div>

											{(() => {
												if(this.state.buttonShowError) {
													return <React.Fragment>
														<Separator style={{ margin: "12px 0" }} />
														<Div style={{"border-radius": "8px", "background": "#eb776e76"}}>
															<Text>
																{this.state.error}
															</Text>
														</Div>
														<Div>
															<Link href="https://vk.com/im?sel=-207901232" target="_blank"> <b>Написать в тех. поддержку</b> </Link>
														</Div>
													</React.Fragment>;
												} else {
													return;
												}
											})()}
										</div>
										<FixedLayout vertical='bottom'><Div>
											<Button after={<Icon56ArrowRightDoorOutline width={24} height={24} />} style={{"color": "#eb4b3fd8"}} size="l" mode="neutral" stretched onClick={this.quotApp}> Выйти из приложения </Button></Div>
										</FixedLayout>
									</Panel>

									<Panel id="whitePage">
										<PanelHeader>Chat Manager</PanelHeader>
									</Panel>

								</View>
							</Root>
						
						{this.state.snackbar}
						
						{(() => {
							if (this.state.activePanel != "updateV" & this.state.activeView != "errors") {
								return <FixedLayout vertical="bottom"><Tabbar 
									itemsLayout="horizontal"
									style={{ position: "static" }}
								>
									<TabbarItem 
										style={{ cursor: "pointer" }}
										selected={this.state.activeView == "home"}
										onClick={() => this.goPage("home", "main")}
										text="Главная"
									>
										<Icon28HomeOutline />
									</TabbarItem>
									<TabbarItem
										style={{ cursor: "pointer" }}
										selected={this.state.activeView == "more"}
										onClick={() => this.goPage("more", "main")}
										text="Ещё"
									>
										<Icon28More />
									</TabbarItem>
								</Tabbar></FixedLayout>;
							} else {
								return;
							}
						})()}
					</SplitCol>
				</SplitLayout>
			</AppRoot>
		);
	}
}

export default App;
